@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap);
* {
  box-sizing: border-box;
}

button {
  border-style: none;
  background-color: transparent;
  outline: none;
  font-size: 1rem;
}

body {
  background-color: #f5f5f5;
  font-family: "Poppins", sans-serif;
  margin: 0;
}

.todos_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.todos_container h1 {
  font-size: 6rem;
  font-weight: 400;
  margin: 0;
  color: #ead7d7;
}

.todoForm {
  color: #e6e6e6;
  background-color: #fff;
  width: 80%;
  height: 10%;
  display: flex;
}

.clearAllBtn {
  padding: 1rem;
  margin-top: 5px;
}

.todo--input {
  border: none;
  background-color: transparent;
  width: 100%;
  font-size: 1.5rem;
}

.todo--input::-webkit-input-placeholder {
  color: #e6e6e6;
  font-size: 1.5rem;
}

.todo--input:-ms-input-placeholder {
  color: #e6e6e6;
  font-size: 1.5rem;
}

.todo--input::-ms-input-placeholder {
  color: #e6e6e6;
  font-size: 1.5rem;
}

.todo--input::placeholder {
  color: #e6e6e6;
  font-size: 1.5rem;
}

.todo--input:focus {
  outline: none;
}

/* todo-list */
.todo-list {
  display: flex;
  width: 80%;
  height: 50px;
  background-color: #fff;
  justify-content: space-between;
}

.todo-clear {
  display: flex;
  position: relative;
  width: 80%;
  margin: 10px;
  margin-left: 20px;
}


.nonClear {
  color: #ead7d7;
  margin-top: -18px;
}

.clearBtn {
  color: rgb(122, 206, 122);
  margin-top: -18px;
}

.completed {
  text-decoration: line-through;
  color: #e6e6e6;
  animation: fadeOut 0.2s alternate;
}

.inCompleted {
  color: black;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
}

@keyframes fadeOut {
  from {
    color: black;
    text-decoration: none;
  }
  to {
    text-decoration: line-through;
    color: #e6e6e6;
  }
}

.deleteBtn {
  margin-right: 20px;
  color:#e6e6e6;
  opacity: 0;
}

.deleteBtn:hover {
  opacity: 1;
  color: #ead7d7;
}

.todo-input {
  position: absolute;
  background-color: transparent;
  border: none;
  top: -30px;
  left: 60px;
  width: 95%;
  height: 240%;
  font-size: 2rem;
}

.todo-input:hover .deleteBtn{
  opacity: 1;
}

/* Todo Form */
.todoOptions {
  width: 80%;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  color: #777777;
  padding: 10px;
}

.todoOptions button {
  cursor: pointer;
  color: #777777;
}


.clearCompleted {
  visibility: hidden;
}

.visible {
  visibility: visible;
}

.todosStatus button {
  margin-left: 8px;
  
}

.todosStatus .selected {
  border: 1px solid #ebcccc;
  border-radius: 4px;
}

